import React from 'react';

const Rule = (props) => {

    const valueToRule = (value, name, seeingDuck) => {
        return {
            'Ace': { title: 'Waterfall', rule: 'Everyone begins drinking, you can stop when the person before you stops' },
            'Two': { title: 'You', rule: `${name} chooses someone to drink` },
            'Three': { title: 'Me', rule: `${name} takes a drink` },
            'Four': { title: 'SeÃ±or', rule: 'Males drink' },
            'Five': { title: 'Webcam Master', rule: `The next time ${name} covers their webcam, everyone else must too. Last one drinks` },
            'Six': { title: 'Chicks', rule: 'Females drink' },
            'Seven': { title: 'Heaven', rule: 'Last one pointing to the sky drinks' },
            'Eight': { title: 'Mate', rule: `${name} picks someone who will drink every time they do` },
            'Nine': { title: 'Rhyme', rule: `${name} picks a word, go round the circle saying a rhyme with it. First to fail drinks` },
            'Ten': { title: 'Categories', rule: `${name} picks a category (e.g. counties), go round the circle giving examples of the category. First to fail drinks` },
            'Jack': { title: 'Quack', rule: `Anyone who sees a duck at the end of this sentence, take a drink ${seeingDuck ? 'ðŸ¦†' : ''}` },
            'Queen': { title: 'Question Master', rule: `${name} is now the question master. If they ask you a question, you must reply with a question or drink.` },
            'King': { title: 'Sing', rule: `The group picks a song. ${name} can sing the chorus or take a drink` }
        }[value]
    }
    const rule = valueToRule(props.value, props.name, props.seeingDuck);
    return (
        <div style={{ textAlign: "center", padding: "4px" }} >
            <strong>{props.name} got a{['Ace', 'Eight'].includes(props.value) ? 'n' : ''} {props.value}</strong><br />
            <strong>{props.value} is {rule.title} :</strong> {rule.rule}
        </div>
    )
}

export default Rule;