import React, { Component } from 'react';
import './App.css';
import Home from './Home';
import Lobby from './Lobby';
import Game from './Game';
import Wakeup from './Wakeup';
import { getCookie, setCookie, deleteCookie } from './Cookies';
import CookieModal from './CookieModal';
import QuitModal from './QuitModal';
import TermsModal from './TermsModal';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      // apiUrl: 'http://192.168.1.168:8000/',
      apiUrl: 'https://immense-retreat-33385.herokuapp.com/',
      home_input_name: '',
      home_input_code: '',
      home_input_join_or_create: 'Join',
      home_input_submit_disabled: false,
      home_errors: {
        name: '',
        code: '',
        input_join_or_create: '',
        submit: '',
        joinGame: '',
        poll_game: ''
      },
      cardPicked: 0,
      seeingDuck: true,
      errors: {
        create_game: '',
        join_game: ''
      },
      acceptedCookies: true,
      pollingAsleep: false,
      isQuitting: false,
      showTerms: false,
      // game: {
      //   "id": "ac261320-7f95-425b-91c3-eaea71de6f80",
      //   "created_at": 1589212991.878647,
      //   "last_updated": 1589213091.779586,
      //   "is_in_lobby": true,
      //   "latest_card_deck_index": 7,
      //   "latest_position": null,
      //   "next_position": 1,
      //   "players": [
      //     {
      //       "name": "Iamsixteenletter",
      //       "is_leader": true,
      //       "is_paused": true
      //     },
      //     {
      //       "name": "mike",
      //       "is_leader": false,
      //       "is_paused": false
      //     },
      //     {
      //       "name": "sammy",
      //       "is_leader": false,
      //       "is_paused": false
      //     },
      //     {
      //       "name": "sammy",
      //       "is_leader": false,
      //       "is_paused": true
      //     }
      //   ],
      //   "angles": [
      //     16,
      //     234,
      //     248,
      //     62,
      //     279,
      //     212,
      //     190,
      //     242,
      //     123,
      //     98,
      //     110,
      //     251,
      //     323,
      //     127,
      //     181,
      //     316,
      //     57,
      //     30,
      //     300,
      //     321,
      //     318,
      //     179,
      //     92,
      //     259,
      //     317,
      //     206,
      //     88,
      //     160,
      //     210,
      //     285,
      //     354,
      //     318,
      //     120,
      //     300,
      //     97,
      //     326,
      //     98,
      //     350,
      //     22,
      //     105,
      //     195,
      //     268,
      //     93,
      //     356,
      //     213,
      //     5,
      //     316,
      //     142,
      //     241,
      //     253,
      //     209,
      //     318
      //   ],
      //   "picks": []
      // },
      player: {
        "id": "829cc7da-b6a4-4e00-bcc0-60227c424c16",
        "updated_at": "2020-05-11T17:35:36.732586+01:00",
        "name": "",
        "position": 4,
        "is_leader": false,
        "is_paused": false
      }
    }
  }

  componentDidMount() {
    this.pollingId = setInterval(() => this.PollGame(), 3000);
    const acceptedCookies = getCookie('accepted');
    if (acceptedCookies === '') {
      this.setState({ acceptedCookies: false })
    }
    const playerId = getCookie('playerId');
    const playerPosition = getCookie('playerPosition');
    const gameId = getCookie('gameId');
    if (playerId !== '' && gameId !== '' && playerPosition !== '') {
      this.getGame(gameId)
        .then(game => {
          this.setState({ game: game });
          return game.players[+playerPosition]
        })
        .then(player =>
          this.setState({
            player: {
              id: playerId,
              is_leader: player.is_leader,
              is_paused: player.is_paused,
              name: player.name,
              updated_at: new Date(),
              position: +playerPosition
            }
          }))

        .catch(error => {
          this.setState({ game: undefined, player: undefined })
        })
    }
  }
  // console.log("cookies: ", playerId, gameId);
  // setInterval(() => {const game = this.state.game; game.latest_card_deck_index = (game.latest_card_deck_index + 1) % 52; this.setState({game: game})}, 1500);
  // }  

  componentWillUnmount() {
    clearInterval(this.pollingId);
  }

  async getData(endpoint) {
    const response = await fetch(this.state.apiUrl + endpoint)
      .then(response => {
        if (response.status !== 200) throw new Error(response.status);
        return response
      });
    return response
  }

  async sendData(endpoint, data, method = 'POST') {
    const response = await fetch(this.state.apiUrl + endpoint, {
      method: method,
      headers: {
        'Content-Type': 'application/json'
      },
      body: JSON.stringify(data)
    }).then(response => {
      if (![200, 201].includes(response.status)) throw new Error(response.status);
      return response
    });
    return response
  }

  async postGameResponse() {
    const response = await this.sendData("games/", {});
    return response
  }

  async postGame() {
    const response = await this.postGameResponse();
    const game = await response.json();
    return game
  }

  async postPlayerResponse(name, game_id) {
    const response = await this.sendData("players/", {
      name: name,
      game: game_id
    });
    return response
  }

  async postPlayer(name, game_id) {
    const response = await this.postPlayerResponse(name, game_id);
    if (response.status !== 201) return null
    const player = await response.json();
    return player
  }

  async getGameResponse(game_id) {
    const response = await this.getData(`games/${game_id}/`);
    return response
  }

  async getGame(game_id) {
    const response = await this.getGameResponse(game_id);
    const game = await response.json();
    return game
  }

  async patchGameResponse(game_id, data) {
    const response = await this.sendData(`games/${game_id}/`, data, 'PATCH');
    return response
  }

  async patchGame(game_id, data) {
    try {
      const response = await this.patchGameResponse(game_id, data);
      const game = await response.json();
      return game
    } catch (error) {
      throw error;
    }
  }

  async patchPlayerResponse(player_id, data) {
    const response = await this.sendData(`players/${player_id}/`, data, 'PATCH');
    return response
  }

  async patchPlayer(player_id, data) {
    const response = await this.patchPlayerResponse(player_id, data);
    const player = await response.json();
    return player
  }

  async createGame() {
    try {
      const newGame = await this.postGame();
      const newPlayer = await this.postPlayer(this.state.home_input_name, newGame.id);
      const updatedGame = await this.getGame(newGame.id);
      this.setState({ game: updatedGame, player: newPlayer });
      this.updateLastEvent();
      setCookie('gameId', updatedGame.id, 60 * 4);
      setCookie('playerId', newPlayer.id, 60 * 4);
      setCookie('playerPosition', newPlayer.position, 8);
    } catch (error) {
      throw error;
    }
  }

  async joinGame() {
    const newPlayer = await this.postPlayer(this.state.home_input_name, this.state.home_input_code);
    if (newPlayer === null) return 'error'
    const updatedGame = await this.getGame(this.state.home_input_code);
    this.setState({ game: updatedGame, player: newPlayer });
    this.updateLastEvent();
    setCookie('gameId', updatedGame.id, 8);
    setCookie('playerId', newPlayer.id, 8);
    setCookie('playerPosition', newPlayer.position, 8);
  }

  async PollGame() {
    if (typeof this.state.game === 'undefined') return;
    if ((+ new Date()) / 1000 > (+ this.state.lastEvent / 1000 + 300)) {
      if (!this.state.pollingAsleep) {
        this.setState({ pollingAsleep: true })
      }
      return;
    }
    this.setState({ pollingAsleep: false });
    this.setState({ home_input_submit_disabled: false });
    const pollResponse = await this.getData(`poll/${this.state.game.id}/`);
    const pollData = await pollResponse.json();
    const lastUpdated = pollData.last_updated;
    if (lastUpdated !== (+ this.state.game.last_updated)) {
      const game = await this.getGame(this.state.game.id);
      if (game.picks.length !== this.state.game.picks.length) {
        this.setState({ seeingDuck: Math.random() > .6 });
      }
      this.setState({ game: game });
      this.updateLastEvent();
    }
  }

  changeHandler = (event) => {
    let nam = event.target.name;
    let val = event.target.value;
    this.setState({ [nam]: val });
  }

  homeSubmitHandler = (event) => {
    event.preventDefault();
    const home_errors = this.state.home_errors;
    let is_valid = true;
    if (this.state.home_input_name.length < 1) {
      home_errors.name = 'Name is required';
      is_valid = false;
    } else {
      home_errors.name = '';
    }
    if (
      this.state.home_input_join_or_create === 'Join'
      && this.state.home_input_code.length !== 36) {
      home_errors.code = '36 character code is required to join a game';
      is_valid = false;
    }
    else {
      home_errors.code = '';
    }
    if (!["Join", "Create"].includes(this.state.home_input_join_or_create)) {
      home_errors.join_or_create = 'Choice of join or create is required';
      is_valid = false;
    }
    else {
      home_errors.join_or_create = '';
    }
    if (!is_valid) {
      this.setState({ home_errors: home_errors });
      return
    }
    this.setState({ home_input_submit_disabled: true });
    if (this.state.home_input_join_or_create === 'Create') {
      this.createGame()
        .catch(error => {
          const home_errors = this.state.home_errors;
          home_errors.submit = 'Cannot create game';
          this.setState({ home_errors: home_errors });
          this.setState({ home_input_submit_disabled: false });
        });
    } else {
      this.joinGame()
        .catch(error => {
          const home_errors = this.state.home_errors;
          home_errors.submit = 'Cannot join game' + (error.message === '400' ? ' make sure the host is in the lobby.' : '');
          this.setState({ home_errors: home_errors });
          this.setState({ home_input_submit_disabled: false });
        });
    }
    return
  }

  async gameStateChangeClickHandler(is_in_lobby) {
    const patchData = { player_id: this.state.player.id, is_in_lobby: is_in_lobby };
    try {
      const updatedGame = await this.patchGame(this.state.game.id, patchData);
      this.setState({ game: updatedGame });
      this.updateLastEvent();
    } catch (error) {
      console.log("COULD NOT UPDATE");
    }
  }

  async restartClickHandler() {
    const patchData = { player_id: this.state.player.id, restart: true };
    try {
      const updatedGame = await this.patchGame(this.state.game.id, patchData);
      this.setState({ game: updatedGame });
      this.updateLastEvent();
    } catch (error) {
      console.log("COULD NOT RESTART");
    }
  }

  async cardPickHandler(card) {
    if (this.state.game.next_position !== this.state.player.position) return
    try {
      this.setState({ seeingDuck: Math.random() > .6 });
      const patchData = { player_id: this.state.player.id, card_index: card };
      const updatedGame = await this.patchGame(this.state.game.id, patchData);
      this.setState({ game: updatedGame });
      this.updateLastEvent();
    } catch (error) {
      console.log("COULD NOT PICK");
    }
  }

  async pauseClickHandler() {
    const patchData = { is_paused: !this.state.player.is_paused };
    const updatedPlayer = await this.patchPlayer(this.state.player.id, patchData);
    this.setState({ player: updatedPlayer });
    this.updateLastEvent();
  }

  updateLastEvent() {
    this.setState({ pollingAsleep: false, lastEvent: new Date() });
  }

  async wakeupClickHandler() {
    this.updateLastEvent();
  }

  acceptCookies() {
    setCookie('accepted', true, 60 * 24 * 1);
    this.setState({ acceptedCookies: true });
  }

  quitHandler() {
    this.pauseClickHandler();
    this.setState({ isQuitting: false });
    deleteCookie('playerId', '/');
    deleteCookie('gameId', '/');
    deleteCookie('position', '/');
    this.setState({ game: undefined });
  }

  quitClickHandler() {
    this.setState({ isQuitting: true });
  }

  render() {

    return (
      <div className="App">

        <TermsModal
          showTerms={this.state.showTerms}
          hideTerms={() => this.setState({ showTerms: false })}
        />

        <QuitModal
          isQuitting={this.state.isQuitting}
          cancelHandler={() => this.setState({ isQuitting: false })}
          quitHandler={this.quitHandler.bind(this)}
        />
        <CookieModal
          acceptedCookies={this.state.acceptedCookies}
          acceptClickHandler={this.acceptCookies.bind(this)}
          showTerms={() => this.setState({ showTerms: true })}
        />

        {((typeof this.state.game === 'undefined') || (this.state.game.players.length === 0)) && <Home
          prefix='home_input_'
          errors={this.state.home_errors}
          name={this.state.home_input_name}
          code={this.state.home_input_code}
          join_or_create={this.state.home_input_join_or_create}
          changeHandler={this.changeHandler.bind(this)}
          submitHandler={this.homeSubmitHandler.bind(this)}
          submitDisabled={this.state.home_input_submit_disabled}
          showTerms={() => this.setState({ showTerms: true })}
        />}


        {
          (typeof this.state.game !== 'undefined')
          &&
          (
            this.state.game.is_in_lobby
              ?
              <Lobby
                game={this.state.game}
                player={this.state.player}
                beginClickHandler={this.gameStateChangeClickHandler.bind(this, false)}
                quitClickHandler={this.quitClickHandler.bind(this)}
              />
              :
              <Game
                game={this.state.game}
                player={this.state.player}
                lobbyClickHandler={this.gameStateChangeClickHandler.bind(this, true)}
                pauseClickHandler={this.pauseClickHandler.bind(this)}
                cardPickHandler={this.cardPickHandler.bind(this)}
                restartClickHandler={this.restartClickHandler.bind(this)}
                seeingDuck={this.state.seeingDuck}
                quitClickHandler={this.quitClickHandler.bind(this)}
              />
          )
        }

        {
          (this.state.pollingAsleep) && <Wakeup
            isAsleep={this.state.pollingAsleep}
            waitTime="5 minutes"
            wakeupClickHandler={this.wakeupClickHandler.bind(this)}
          />
        }

      </div >
    );
  }
}

export default App;
