import React from 'react';
import Container from 'react-bootstrap/Container';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import Button from 'react-bootstrap/Button';
import PlayerList from './PlayerList';
import Board from './Board';
import Rule from './Rule';
import QuitButton from './QuitButton';

const Game = (props) => {
    const indexToValue = (card_index) => {
        return ['Ace', 'Two', 'Three', 'Four', 'Five', 'Six', 'Seven', 'Eight',
            'Nine', 'Ten', 'Jack', 'Queen', 'King'][card_index % 13]
    }

    return (
        <Container style={{ height: "200vh" }}>
            <Row>
                <Col className="d-none d-lg-block" lg={12} xl={12}>
                    <div style={{ height: "10vh" }}></div>
                </Col>
                <Col className="d-none d-md-block" md={3} lg={2} xl={2}></Col>
                <Col sm={12} md={6} lg={5} xl={5} style={{ border: " solid 0px rgba(255,255,255,0.5)" }}>
                    <div style={{ textAlign: "center", borderRadius: ".25rem", border: " solid 2px rgba(255,255,255,0.0)" }} >
                        <Board
                            game={props.game}
                            sideLength={300}
                            radius={100}
                            sidePadding={20}
                            cardWidth={50}
                            cardHeight={70}
                            cardCornerRadius={3}
                            onCardClickHandler={props.cardPickHandler}
                        />
                        {
                            (props.game.latest_position !== null) && <Rule
                                name={props.game.players[props.game.latest_position].name}
                                value={indexToValue(props.game.latest_card_deck_index)}
                                seeingDuck={props.seeingDuck}
                            />
                        }
                    </div>
                </Col>
                <Col md={3} lg={0} className="d-lg-none d-xl-none" />
                <Col md={3} lg={0} className="d-lg-none d-xl-none" />
                <Col sm={12} md={6} lg={5} xl={5} style={{ border: " solid 0px rgba(0,0,0,0.5)" }}>
                    <div style={{ borderRadius: ".25rem", border: " solid 2px rgba(255,255,255,0.0)" }} >
                        <h4 style={{ fontWeight: 900, paddingTop: "3vmin", marginBottom: "0", paddingBottom: "10px" }}>Players</h4>
                        <PlayerList players={props.game.players} nextPosition={props.game.next_position} me_index={props.player.position} />
                        <br />
                        <Button variant="secondary" onClick={props.pauseClickHandler}>
                            {props.player.is_paused ? 'Un-Pause' : 'Pause'} me
                        </Button>
                        <p style={{ color: "#ccc", fontSize: "smaller", marginBottom: "8px" }}>While paused, your turns are skipped</p>

                        <QuitButton quitClickHandler={props.quitClickHandler} />

                        {props.player.is_leader && <div style={{ border: "solid 2px #888", borderRadius: "5px", padding: "5px" }}>
                            <h3>Host Options</h3>
                            <Button variant="secondary" onClick={props.lobbyClickHandler}>
                                Go to lobby
                            </Button>
                            <p style={{ color: "#ccc", fontSize: "smaller" }}>In the lobby, more players can join</p>
                            <Button variant="secondary" onClick={props.restartClickHandler}>
                                Restart
                            </Button>
                            <p style={{ color: "#ccc", fontSize: "smaller" }}>Put all the cards back</p>
                        </div>
                        }

                    </div>
                </Col>
            </Row>
        </Container >
    )
}

export default Game;