import React from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';

const CookieModal = (props) => {
    return (
        <Modal show={!props.acceptedCookies} onHide={() => console.log("HIDDEN")} animation={false}>
            <Modal.Header>
                <Modal.Title>Cookies</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                This site uses cookies in accordance with the
                <button onClick={props.showTerms} style={{ border: 'none', backgroundColor: 'rgba(0,0,0,0)', color: '#57f' }}>terms</button>
                to function and for analytics.
            </Modal.Body>
            <Modal.Footer>
                <Button variant="success" onClick={props.acceptClickHandler}>
                    Accept All Cookies
                </Button>
            </Modal.Footer>
        </Modal>
    )
}

export default CookieModal;