import React from 'react';
import Card from './Card';

const Board = (props) => {
    const centre = props.sideLength / 2;
    return (
        <svg height={props.sideLength} width={props.sideLength} className="board">
            <g transform="scale(1 1)">
                <defs>
                    <pattern id="faceDown" x="0" y="0" width="9" height="9" patternUnits="userSpaceOnUse" >
                        <rect x="0" y="0" height="9" width="9" fill="#227" />
                        <g transform="rotate(45, 5, 5)">
                            <rect x="2" y="2" height="5" width="5" fill="#bbb" />
                        </g>
                    </pattern>
                    <pattern id="faceUp" x="0" y="0" width="10" height="10" patternUnits="userSpaceOnUse" >
                        <rect x="0" y="0" height="10" width="10" fill="#eee" />
                    </pattern>
                </defs>
                {
                    props.game.angles.map((angle, index) => (
                        <g key={index} transform={"rotate(" + angle + ", " + centre + "," + centre + ")"}>
                            {
                                (!props.game.picks.includes(index)) && <Card
                                    onClickHandler={props.onCardClickHandler}
                                    fill="url(#faceDown)"
                                    left_x={props.sideLength / 2 - props.cardWidth / 2}
                                    top_y={props.sidePadding}
                                    width={props.cardWidth}
                                    height={props.cardHeight}
                                    cornerRadius={props.cardCornerRadius}
                                    faceUp={false}
                                    card={index}
                                />
                            }
                        </g>
                    ))
                }
                <g className="latestCard">
                    {
                        props.game.latest_card_deck_index !== null ?
                            <Card
                                fill="url(#faceUp)"
                                left_x={centre - props.cardWidth / 2}
                                top_y={centre - props.cardHeight / 2}
                                width={props.cardWidth}
                                height={props.cardHeight}
                                cornerRadius={props.cardCornerRadius}
                                faceUp={true}
                                card={+ props.game.latest_card_deck_index}
                            />
                            : null
                    }

                </g>
            </g>
        </svg>
    )
}

export default Board;