import React from 'react';
import Badge from 'react-bootstrap/Badge';

const Player = (props) => {
    return (
        <li>
            {props.player.name}
            {props.player.is_me ?
                <span className="badgeContainer">
                    <Badge pill variant="warning" >
                        me
                </Badge>
                </span> : null}
            {props.player.is_leader ?
                <span className="badgeContainer">
                    <Badge pill variant="secondary" title="Hosts can restart games, open lobbies, and pause players">
                        host
                </Badge>
                </span> : null}
            {props.player.is_paused ?
                <span className="badgeContainer">
                    <Badge pill variant="secondary" title="While paused, this players turns are skipped">
                        paused
                </Badge>
                </span> : null}
            {props.player.is_next ?
                <span className="badgeContainer">
                    <Badge pill variant="success" >
                        picking
                </Badge>
                </span> : null}
        </li>
    )

}

export default Player;