import React from 'react';
import Container from 'react-bootstrap/Container';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';

const Home = (props) => {
    return (
        <Container style={{ height: '100vh' }}>

            <Row>
                <Col md={2} lg={3} xl={4}></Col>
                <Col sm={12} md={8} lg={6} xl={4}>
                    <h1 style={{ fontWeight: 900, paddingTop: '12vmin', paddingBottom: '0' }}>Ring of Cards</h1>
                </Col>
                <Col md={2} lg={3} xl={4}></Col>
            </Row>

            <Row>
                <Col xs={2} md={2} lg={3} xl={4}></Col>
                <Col xs={8} sm={10} md={8} lg={6} xl={4}>
                    <Form noValidate onSubmit={props.submitHandler}>
                        <Form.Group controlId={props.prefix + 'name'} style={{ paddingTop: '8vh' }}>
                            <Form.Label>Name</Form.Label>
                            <Form.Control
                                placeholder='Pick a name'
                                name={props.prefix + 'name'}
                                value={props.name}
                                onChange={props.changeHandler}
                                maxLength='16'
                                required
                            />
                            <Form.Text className="error">
                                {props.errors.name}
                            </Form.Text>
                        </Form.Group>
                        <Form.Group controlId='formType' style={{ paddingTop: '1vh' }}>
                            {
                                [{ value: 'Join', label: 'Join a game' }, { value: 'Create', label: 'Create a new game' }].map((option) => {
                                    return <Form.Check
                                        type='radio'
                                        id={'radio-' + option.value}
                                        key={option.value}
                                        label={option.label}
                                        value={option.value}
                                        checked={props.join_or_create === option.value}
                                        name={props.prefix + 'join_or_create'}
                                        onChange={props.changeHandler}
                                    />
                                })
                            }
                            <Form.Text className="error">
                                {props.errors.join_or_create}
                            </Form.Text>
                        </Form.Group>
                        <Form.Group
                            controlId={props.prefix + 'code'}
                            style={{
                                paddingTop: '1vh',
                                display: props.join_or_create === 'Join' ? 'block' : 'none'
                            }}
                        >
                            <Form.Label>Game code</Form.Label>
                            <Form.Control
                                placeholder='36 characters'
                                name={props.prefix + 'code'}
                                value={props.code}
                                onChange={props.changeHandler}
                                maxLength='36'
                            />
                            <Form.Text className="error">
                                {props.errors.code}
                            </Form.Text>
                        </Form.Group>
                        <Form.Group controlId='formSubmit' style={{ paddingTop: '1vh' }}>
                            <Button variant='success' type='submit' disabled={props.submitDisabled}>
                                {props.join_or_create}
                            </Button>
                            <Form.Text>
                                By playing you agree to the
                                <button style={{ border: 'none', backgroundColor: 'rgba(0,0,0,0)', color:'#57f' }} onClick={props.showTerms}>
                                    terms and privacy policy
                                </button>
                                and that you are at least 21 years of age. Always play responsibly, water is recommended as a drink.
                            </Form.Text>
                            <Form.Text className="error">
                                {props.errors.submit}
                            </Form.Text>
                        </Form.Group>
                    </Form>
                </Col>
            </Row>
        </Container>
    )
}

export default Home;