import React from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';

const QuitModal = (props) => {
    return (
        <Modal show={props.isQuitting} onHide={props.cancelHandler} animation={false}>
            <Modal.Header>
                <Modal.Title>Quit?</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                You will be permanently paused in this game. You can return to it by joining again.
            </Modal.Body>
            <Modal.Footer>
                <Button variant="danger" onClick={props.quitHandler}>
                    Quit
                </Button>
                <Button variant="success" onClick={props.cancelHandler}>
                    Stay
                </Button>
            </Modal.Footer>
        </Modal>
    )
}

export default QuitModal;