import React from 'react';
import Button from 'react-bootstrap/Button';
import Modal from 'react-bootstrap/Modal';

const Wakeup = (props) => {
    return (
        <Modal show={props.isAsleep} onHide={props.wakeupClickHandler} animation={false}>
            <Modal.Header closeButton>
                <Modal.Title>Are you still there?</Modal.Title>
            </Modal.Header>
            <Modal.Body>
                Nothings happened in the past {props.waitTime} so I stopped checking for updates.
                Close this pop-up and I will start checking again.
            </Modal.Body>
            <Modal.Footer>
                <Button variant="secondary" onClick={props.wakeupClickHandler}>
                    Close
                </Button>
            </Modal.Footer>
        </Modal>
    )
}

export default Wakeup;