import React from 'react';
import Player from './Player';

const PlayerList = (props) => {
    return (
        <ol className="PlayerList">
            {props.players.map((player, index) => <Player key={index} player={{...player, is_next: index === props.nextPosition, is_me: index === props.me_index}} />)}
        </ol>
    )

}

export default PlayerList;