import React from 'react';
import Container from 'react-bootstrap/Container';
import Col from 'react-bootstrap/Col';
import Row from 'react-bootstrap/Row';
import Form from 'react-bootstrap/Form';
import Button from 'react-bootstrap/Button';
import PlayerList from './PlayerList';
import QuitButton from './QuitButton';

const Lobby = (props) => {
    return (
        <Container style={{ height: "100vh" }}>
            <Row>
                <Col md={2} lg={3} xl={4}></Col>
                <Col sm={12} md={8} lg={6} xl={4}>
                    <h1 style={{ fontWeight: 900, paddingTop: "3vmin", paddingBottom: "3vmin" }}>Lobby</h1>
                </Col>
            </Row>
            <Row>
                <Col md={2} lg={3} xl={4}></Col>
                <Col sm={12} md={8} lg={6} xl={4}>
                    <small style={{ textAlign: "left", marginBottom: "0", paddingBottom: "0" }}>{props.game.id}</small>
                    <p style={{ color: "#ccc", fontSize: "smaller" }}>Share this code to invite friends</p>
                </Col>
            </Row>

            <Row>
                <Col md={2} lg={3} xl={4}></Col>
                <Col sm={12} md={8} lg={6} xl={4}>

                    <QuitButton quitClickHandler={props.quitClickHandler} />

                    {
                        props.player.is_leader ?

                            <Form.Group controlId="formSubmit">
                                <Button variant="success" type="submit" onClick={props.beginClickHandler}>
                                    Go to game
                        </Button>
                                <p style={{ color: "#ccc", fontSize: "smaller" }}>Only the host (you) can see this button.</p>
                            </Form.Group>
                            :
                            <h5>
                                <span>
                                    <svg className="spinner" height="16" width="16">
                                        <circle cx="8" cy="8" r="6" stroke="#888" strokeWidth="3" strokeDasharray="20,12" strokeLinecap="round" fill="none" />
                                    </svg>
                                </span>
                                <span style={{paddingLeft: "4px"}}>Waiting for host to close the lobby</span>
                           </h5>
                    }
                </Col>
            </Row>


            <Row>
                <Col md={2} lg={3} xl={4}></Col>
                <Col sm={12} md={8} lg={6} xl={4}>
                    <h2 style={{ fontWeight: 900, paddingTop: "3vmin", marginBottom: "0", paddingBottom: "0" }}>Players</h2>
                    <p style={{ color: "#ccc", fontSize: "smaller" }}>Lobby can be opened mid-game to let more players in.</p>
                    <PlayerList players={props.game.players} me_index={props.player.position} />
                </Col>
            </Row>
        </Container>

    )
}

export default Lobby;